.header {
  width: 100%;
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.list {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.list a {
  font-size: 1.3rem;
  text-decoration: none;
  color: var(--color-grey-900);
}

.list a:hover,
.list a.active {
  color: var(--color-brown-900);
  font-weight: 500;
}

.buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.primary-btn {
  background-color: var(--color-brown-900);
  color: white;
  border: none;
  border-radius: 2rem;
  font-size: 1em;
  white-space: nowrap;
  padding: 0.75rem 1rem;
}

.secondary-btn {
  background-color: white;
  color: var(--color-brown-900);
  box-shadow: inset 0 0 0 2px var(--color-brown-900);
  border-radius: 2rem;
  font-size: 1em;
  padding: 0.75rem 1.5rem;
  border: none;
}

.primary-btn:hover,
.primary-btn:active {
  background-color: var(--color-brown-800);
}
.secondary-btn:hover,
.secondary-btn:active {
  background-color: var(--color-brown-100);
}

@media (max-width: 450px) {
  .header {
    width: 20rem;
    padding: 0.5rem 1rem;
  }

  .list a {
    font-size: 0.8rem;
    text-decoration: none;
    color: var(--color-grey-900);
  }

  .primary-btn {
    font-size: 0.5em;
    padding: 0.5rem 1rem;
  }

  .secondary-btn {
    box-shadow: inset 0 0 0 1px var(--color-brown-900);
    font-size: 0.75em;
    padding: 0.35rem 1rem;
  }
}
