/* LoginPage.module.css */
.container {
  width: 25rem;
  margin: 4rem auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 1rem;
  text-align: center;
}

@media (max-width: 450px) {
  .container {
    width: 20rem;
  }
}

h1 {
  margin: 0.75rem 0 0.75rem;
}

.form {
  display: flex;
  flex-direction: column;
}

.error-dialog {
  background-color: #ffacac;
  border-radius: 10px;
  padding: 10px;
  border: 1px solid salmon;
  margin: 10px;
}

/* Styles for the error message */
.error-message {
  color: red;
  margin: 10px 0;
}

.formGroup {
  margin-bottom: 15px;
}

label {
  font-weight: 400;
  text-align: start;
}

.input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.button {
  margin-top: 1rem;
  background-color: var(--color-brown-900);
  color: white;
  border: none;
  border-radius: 0.5rem;
  padding: 10px;
  cursor: pointer;
}

.button:hover,
.button:active {
  background-color: var(--color-brown-800);
}

.links {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.forgotPassword {
  text-decoration: none;
  color: var(--color-brown-900);
}

.or {
  margin: 0 10px;
}

.signUp {
  text-decoration: none;
  color: var(--color-brown-900);
}
